import React, { useRef } from "react";
import LogoImg from "../Logo.svg";
import FullscreenImg from "./Fullscreen.svg";
import InstallImg from "./Install.svg";
import "./Header.css";

const Header = () => {
  const installBtn = useRef(null);
  let deferredPrompt;
  let installInterval;

  const install = () => {
    deferredPrompt.prompt();
    // Wait for the user to respond to the prompt
    deferredPrompt.userChoice.then(choiceResult => {
      if (choiceResult.outcome === "accepted") {
        console.log("User accepted the A2HS prompt");
      } else {
        console.log("User dismissed the A2HS prompt");
      }
      deferredPrompt = null;
      clearInterval(installInterval);
    });
  };

  window.addEventListener("beforeinstallprompt", e => {
    deferredPrompt = e;

    installInterval = setInterval(() => {
      installBtn.current.style.animationName = installBtn.current.style
        .animationName
        ? undefined
        : "Icon-shake";
    }, 1000);
  });

  return (
    <div className="Header">
      <img className="Header-logo" src={LogoImg} alt="Logo" />
      <div className="Header-title">Scan Buy</div>
      <img
        className="Header-fullscreen Header-img"
        alt="Fullscreen"
        src={FullscreenImg}
        onClick={() => {
          document.body.requestFullscreen();
        }}
      />
      <img
        className="Header-install Header-img Icon-can-shake"
        alt="Install"
        src={InstallImg}
        ref={installBtn}
        onClick={install}
      />
    </div>
  );
};

export default Header;
